<template>
  <profile-template class="profile gray2">
    <div class="image-box profile-imagem-main gray1 mt-8 elevation-1">
      <img
        v-if="user.image_url"
        :src="userImageUrl"
        class="profile-image"
      />
      <btn-icon
        icon="photo_camera"
        class="success btn-upload elevation-1"
        :small="false"
        @clicked="uploadProfileImage"
      />
      <upload-image
        ref="profileImage"
        :uploadUrl="uploadImageUrl"
        :cropUploadFail="onUploadProfileImageFail"
        @onCropUploadSuccess="onUploadProfileImageSuccess"
      />
    </div>
    <div class="mt-8" v-if="user">
      <input-field
        id="name"
        label="Nome"
        :rules="nameRules"
        :value="user.name"
        icon="person"
        :success="isNameValid(name)"
        @onKeyUp="onKeyUp" 
        @onKeyUpEnter="focusToNext('phoneNumber')" 
      />
      <phone-field
        id="phoneNumber"
        label="Telefone"
        icon="phone"
        :success="isPhoneNumberValid(phoneNumber)"
        :value="this.setPhoneMask(phoneNumber)"
        :required="true"
        @onKeyUp="onKeyUp" 
      />
      <date-picker
        id="bornDate"
        label="Data de Nascimento"
        :success="true"
        :value="user.born_date"
        :max="maxDate"
        @change="onKeyUp"
      />
    </div>
    <div class="mt-8 mb-4 mx-sm-12 text-center">
      <btn-secondary
        text="Salvar"
        dark
        :disabled="btnDisabled"
        :loading="loading"
        @clicked="save"
      />
      <btn-text
        class="mt-4"
        text="Cancelar"
        @clicked="goToBack"
      />
    </div>
  </profile-template>
</template>

<script>
import BtnSecondary from '@/components/globais/buttons/secondary/BtnSecondary'
import BtnIcon from '@/components/globais/buttons/icon/BtnIcon'
import BtnText from '@/components/globais/buttons/text/BtnText'
import DatePicker from '@/components/globais/form/date-picker/DatePicker'
import InputField from '@/components/globais/form/input-field/InputField'
import PhoneField from '@/components/globais/form/phone-field/PhoneField'
import ProfileTemplate from '@/components/site/ProfileTemplate'
import UploadImage from '@/components/globais/upload-image/UploadImage'
import DateMixin from '@/mixins/DateMixin'
import FormMixin from '@/mixins/FormMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import TextMixin from '@/mixins/TextMixin'
import UserMixin from '@/mixins/UserMixin'
import ValidationMixin from '@/mixins/ValidationMixin'

export default {
  name: 'Profile',
  components: { BtnSecondary, BtnIcon, BtnText, DatePicker,InputField, PhoneField, ProfileTemplate, UploadImage },
  mixins: [ DateMixin, FormMixin, SnackbarMixin, TextMixin, UserMixin, ValidationMixin ],
  data: (vm) => ({
    bornDate: null,
    name: null,
    phoneNumber: null,
    nameRules: [ value => !!value || 'Informe o nome' ],
    loading: false,
    uploadImageUrl: `${vm.$url_api}/user/image`
  }),
  computed: {
    btnDisabled () {
      let phoneNumber = this.removeMask(this.phoneNumber)
      if (!this.name || phoneNumber.length < 10) return true
      return false
    },
    maxDate () {
      return this.formatDateToDateEn(new Date())
    },
    userToken () {
      return localStorage.userToken
    },
    userImageUrl () {
      return `${this.$url_backend}${this.user.image_url}`
    }
  },
  mounted () {
    this.name = this.user.name
    this.phoneNumber = this.user.phone_number
    this.bornDate = this.user.born_date || this.formatDateToDateEn(new Date())
  },
  methods: {
    save () {
      this.loading = true
      let date = new Date()
      date.setFullYear(date.getFullYear() - 18)
      if (date < new Date(this.parseDate(this.bornDate))) {
        this.setErro('O Meu Barbeiro só é permitido para maiores de idade!');
      } else {
        this.$axios.put(`user/${this.user.id}`, {
          born_date: this.bornDate,
          name: this.name,
          phone_number: this.removeMask(this.phoneNumber)
        })
        .then(response => {
          if (response.data.codigo === 200) {
            this.showSnackbar('Perfil salvar com sucesso!')
            this.storeUser(response.data.corpo)
            this.goToBack()
          } else this.setErro(response.data.corpo)
        })
        .catch (() => this.setErro('Não foi possível alterar o perfil!'))
      }
    },
    onKeyUp (value, id) {
      this.$data[id] = value
    },
    onUploadProfileImageFail () {
      this.showSnackbarErro('Não foi possível salvar a imagem!')
    },
    onUploadProfileImageSuccess (response) {
      this.storeUser(response)
      this.$refs.profileImage.hideDialog()
      this.showSnackbar('Imagem atualizada com sucesso!')
    },
    setErro (msg) {
      this.loading = false
      this.showSnackbarErro(msg)
    },
    uploadProfileImage () {
      this.$refs.profileImage.showDialog(this.userToken)
    }
  }
}
</script>

<style lang="scss" scoped>
@import 'src/scss/theme.scss';

.image-box {
  position: relative;
  width: 200px;
  height: 200px;
  margin: auto;
  border: 3px solid #000 !important;
  border-radius: 50%;
}

.btn-upload {
  position: absolute;
  bottom: 0;
  right: 0;
}
.profile-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  max-width: 100%;
  border-radius: 50%;
}
</style>